.btn {
	cursor: pointer;
}

.hide {
	display: none;
}

.show {
	display: block;
}

.commentContainer {
	overflow-wrap: break-word;
	white-space: pre-wrap;
	/* border: 2px solid black; */
	margin: 0 auto;
	margin-top: 15px;
	max-width: 400px;
	padding: 10px;
	/* background-color: rgb(209, 174, 17); */
	border-radius: 10px;
}

.commentUser {
	padding-top: 10px;
	display: flex;
	align-items: center;
	justify-content: center;
}

#spinnerContainer {
	overflow-wrap: break-word;
	white-space: pre-wrap;
	border: 2px solid black;
	background: none;
	border: none;
	margin: 0 auto;
	margin-top: 15px;
	max-width: 400px;
	padding: 10px;
	border-radius: 10px;
}

.username {
	cursor: pointer;
	color: rgb(0, 119, 255);
	border: none;
	outline: none;
	background: none;
	padding: 0;
	font-size: 18px;
	font-family: "Kumbh Sans", sans-serif;
	padding-left: 10px;
	padding-right: 10px;
}

.username:hover {
	text-decoration: underline;
}

.text {
	font-size: 18px;
	font-family: "Kumbh Sans", sans-serif;
}

.commentAvatar {
	border-radius: 10px;
	cursor: pointer;
}

.timeSpan {
	font-size: 17px;
	font-family: "Kumbh Sans", sans-serif;
}

.noComments {
	font-size: 17px;
	font-family: "Kumbh Sans", sans-serif;
}

.notClickable {
	cursor: default;
	pointer-events: none;
}

.notClickable:hover {
	text-decoration: none;
}

.noAvatar {
	height: 60px;
	width: 60px;
	color: white;
	background-color: rgb(37, 37, 37);
	display: flex;
	justify-content: center;
	align-items: center;
	cursor: pointer;
	border-radius: 10px;
}
